.animate-color {
  animation: colorChange 3s ease; /* Runs for 1 second */
}

@keyframes colorChange {
  0%,
  100% {
    color: black; /* Original color */
  }
  50% {
    color: rgb(225, 255, 0); /* Temporary color */
  }
}
.flash-color {
  animation: flash 3s ease; /* Runs for 1 second */
  font-size: 84px;
  margin-left: 62px;
  margin-top: 36px;
}

@keyframes flash {
  0%,
  100% {
    color: black; /* Original color */
  }
  50% {
    color: rgb(255, 255, 255); /* Temporary color */
  }
}
